* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    letter-spacing: .03em; 
}

body{
    background-image: linear-gradient(to right bottom, #00fffe 20%, #00bbfe 50%, #0077fe 70%, #0033fe 100%);
    background-attachment: fixed;
}

.signup_wrap{
    width: 320px;
	height: 400px;
	background-color: #ffffff;
	position: absolute;
	left: 75%;
	top: 50%;
	transform: translate(-50%, -50%);
	border-radius: 10px;
    border-style: solid;
    border-color: #5f5c5c;
	padding: 10px 20px; 
}

.signup_title {
    font-size: 25px;
    text-align: left;
    color: #000000;
    margin-bottom: 5px;
}

.line {
    height: 1px;  
    background-color: #5f5c5c;
    margin-bottom: 20px;
}

.input_name{
    margin-bottom: 10px;
}

.input_password1{
    margin-bottom: 10px;
}

.input_password2{
    margin-bottom: 20px;
}

.signup_button{
    width:100%;
    margin-bottom: 10px;
}

